<template>
  <div>
    <h2 class="text-center text-primary">Q&A 목록</h2>
    <div class="text-right">
      <div class="form-group input-group-append mt-3 mb-5" style="float: right">
        <label for="sel1" class="mr-2"><big>검색: </big></label>
        <select
          class="form-control"
          id="sel1"
          v-model="key"
          style="width: 100px"
        >
          <option disabled value="">선택</option>
          <option value="title">제목</option>
          <option value="userid">작성자</option>
        </select>
        <input
          type="text"
          class="form-control"
          placeholder="검색어 입력"
          v-model="word"
          v-on:keyup.enter="search"
          style="width: 300px"
        />
        <button class="btn btn-primary ml-3" type="button" @click="search">
          검색
        </button>
        <button class="btn btn-success ml-1" @click="mvregist" v-if="loginId">
          등록
        </button>
      </div>
    </div>
    <div>
      <table
        class="table table-hover"
        style="margin-left: auto; margin-right: auto"
      >
        <colgroup>
          <col width="10%" />
          <col width="30%" />
          <col width="20%" />
          <col width="20%" />
          <col width="20%" />
        </colgroup>
        <thead>
          <tr>
            <th>번호</th>
            <th>제목</th>
            <th>작성자</th>
            <th>날짜</th>
            <th>답변 여부</th>
          </tr>
        </thead>
        <tbody v-if="Qnas.length > 0">
          <tr
            v-for="(qna, index) in Qnas"
            :key="index"
            @click="mvSearch(qna.no)"
          >
            <td>{{ qna.no }}</td>
            <td>
              {{ qna.title }}
            </td>
            <td v-text="qna.userid"></td>
            <td>{{ qna.regdate | toDate }}</td>
            <td v-if="qna.repl" class="text-success">답변 완료</td>
            <td v-else class="text-warning">미답변</td>
          </tr>
        </tbody>
        <tr
          v-if="Qnas.length == 0"
          class="text-danger mt-5 text-center"
          style="margin-left: auto; margin-right: auto"
        >
          <td colspan="5">
            <h4 class="text-center mt-5">등록된 QnA가 없습니다.</h4>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>
<script>
import moment from "moment";
import { mapGetters } from "vuex";
export default {
  name: "QnA",
  data() {
    return {
      key: "",
      word: "",
    };
  },
  filters: {
    toDate(regdate) {
      return moment(new Date(regdate)).format("YYYY.MM.DD");
    },
  },
  computed: {
    ...mapGetters(["Qnas", "loginId"]),
  },
  created() {
    this.searchAll();
    this.getLoginUser();
  },
  methods: {
    mvregist() {
      this.$router.push("/create");
    },
    mvSearch(no) {
      this.$router.push(`/search?no=${no}`);
    },
    search() {
      // 검색할 결과를 담을 배열

      // 만약 검색어가 없을 때
      if (!this.word || this.word == "") {
        this.searchAll();
      } else {
        // 검색어가 있다면
        this.$store.dispatch(
          "getQnas",
          `http://localhost/qna.do?key=${this.key}&word=${this.word}`
        );
      }
    },
    searchAll() {
      this.$store.dispatch("getQnas", `http://localhost/qna.do/`);
    },
    getLoginUser() {
      this.$store.dispatch("getLoginId");
    },
  },
};
</script>
