<template>
  <div>
    <h2 class="text-center text-primary">Q&A 등록</h2>
    <table
      class="table table-condensed w-25 my-5"
      style="margin-left: auto; margin-right: auto"
    >
      <tr>
        <th>글쓴이</th>
        <td>{{ loginId }}</td>
      </tr>
      <tr>
        <th>제목</th>
        <td>
          <input
            type="text"
            id="title"
            ref="title"
            v-model="title"
            placeholder="제목을 입력해주세요"
          />
        </td>
      </tr>
      <tr>
        <th>내용</th>
        <td>
          <textarea
            id="content"
            cols="30"
            ref="content"
            v-model="content"
            rows="10"
          ></textarea>
        </td>
      </tr>
    </table>
    <div class="text-right">
      <button class="btn btn-primary" @click="createHandler">등록</button>
      <button class="btn btn-warning ml-2" @click="moveHandler">목록</button>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      title: "",
      content: "",
    };
  },
  computed: {
    ...mapGetters(["loginId"]),
  },
  created() {
    this.$store.dispatch("getLoginId");
  },
  methods: {
    moveHandler() {
      this.$router.push("/qna");
    },
    createHandler() {
      // 데이터 검증
      let err = true;
      let msg = "";

      !this.title &&
        ((msg = "제목을 입력해주세요"),
        (err = false),
        this.$refs.title.focus());
      err &&
        !this.content &&
        ((msg = "내용을 입력해주세요"),
        (err = false),
        this.$refs.content.focus());

      if (!err) {
        alert(msg);
      } else {
        axios
          .post("http://localhost/qna.do", {
            userid: this.loginId,
            title: this.title,
            contents: this.content,
          })
          .then(({ data }) => {
            if (data == 1) {
              alert("등록이 완료 됐습니다.");
              this.moveHandler();
            } else {
              alert("등록 실패!!!");
            }
          })
          .catch((err) => {
            console.log(err);
            alert("에러 발생");
          });
      }
    },
  },
};
</script>
