<template>
  <div>
    <h2 class="text-center text-primary">Q&A 게시판</h2>
    <table
      class="table table-condensed w-25 mt-5"
      style="margin-left: auto; margin-right: auto"
      v-if="Qna"
    >
      <tr>
        <th>제목</th>
        <td v-text="Qna.title"></td>
      </tr>
      <tr>
        <th>작성자</th>
        <td v-text="Qna.userid"></td>
      </tr>
      <tr>
        <th>작성일</th>
        <td>{{ Qna.regdate | toDate }}</td>
      </tr>
      <tr style="height: 100px">
        <th>내용</th>
        <td v-text="Qna.contents"></td>
      </tr>
      <tr style="height: 100px" v-if="Qna.repl">
        <th>답변</th>
        <td v-text="Qna.repl"></td>
      </tr>
      <tr style="height: 100px" v-else-if="!Qna.repl && Qna.loginId != 'admin'">
        <th>답변</th>
        <td class="text-danger">등록된 답변이 없습니다!</td>
      </tr>
      <template
        style="height: 100px"
        v-else-if="!Qna.repl && Qna.loginId == 'admin'"
      >
        <th>답변</th>
        <td class="input-group-append">
          <textarea cols="30" rows="5" ref="re" v-model="reply"></textarea>
          <button class="btn btn-primary ml-2" @click="registRe">등록</button>
        </td>
      </template>
    </table>
    <div class="mb-3 text-right">
      <router-link class="btn btn-warning" to="/qna">목록</router-link>
      <button
        class="btn btn-success ml-2"
        @click="update()"
        v-if="Qna.userid == Qna.loginId && !Qna.repl"
      >
        수정
      </button>
      <button
        class="btn btn-danger ml-2"
        @click="remove()"
        v-if="Qna.userid == Qna.loginId || Qna.loginId == 'admin'"
      >
        삭제
      </button>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { mapGetters } from "vuex";
import moment from "moment";
export default {
  data() {
    return {
      reply: "",
    };
  },
  filters: {
    toDate(regdate) {
      return moment(new Date(regdate)).format("YYYY.MM.DD");
    },
  },
  computed: {
    ...mapGetters(["Qna"]),
  },
  created() {
    this.$store.dispatch(
      "getQna",
      `http://localhost/qna.do/${this.$route.query.no}`
    );
  },
  methods: {
    update() {
      this.$router.push("/update");
    },
    remove() {
      this.$router.push("/remove");
    },
    registRe() {
      if (!this.reply) {
        alert("답변을 입력하세요");
        this.$refs.re.focus();
      } else {
        console.log(this.reply);
        console.log(this.Qna.title);
        console.log(this.Qna.contents);
        console.log(this.Qna.no);
        axios
          .put(`http://localhost/qna.do/${this.Qna.no}`, {
            title: this.Qna.title,
            contents: this.Qna.contents,
            repl: this.reply,
          })
          .then(({ data }) => {
            console.log(data);
            if (data == 1) {
              alert("답변 등록 완료!!");
              this.moveHandler();
            } else {
              alert("답변 등록 중 오류 발생!");
            }
          })
          .catch((err) => {
            console.log(err);
            alert("오류 발생!!");
          });
      }
    },
    moveHandler() {
      this.$router.push("/qna");
    },
  },
};
</script>
